import React, {useState} from 'react';
import {
    ControlLabel,
    Form,
    FormControl,
    FormGroup,
    InputNumber,
    Schema
} from 'rsuite';
import Modal from '../../components/Modal';
import CustomField from '../../components/client/Form/CustomField/CustomField';
import {useIntl} from "react-intl";
import Checkbox from 'rsuite/es/Checkbox';
import {ALLOCATION_PRICE_RANGE_DIALER_API} from "../../const/apiMethods";
import {useWindowWidth} from "../../hooks";
import styled, {css} from 'styled-components';
import {SCREEN_MEDIA} from "../../const";
import TrunksList from '../../components/TrunksList/TrunksList';
import m from "../../definedMessages";
import {CustomModalClient} from "../../components/base";
import {BaseCheckboxField, BaseInputNumberField} from "../../components/base/BaseForm";

const {md} = SCREEN_MEDIA;

const {StringType, NumberType} = Schema.Types;

export default ({
    show,
    onClose,
    onSend,
    trunks,
    priceData,
    maxWidth = 640,
    loading,
    formValue,
    randomNumber,
    onChangeRandomNumber,
    onChangeFormValue,
    allocationLimit,
    spAuth,
    ...props
}) => {

    let form;
    const resizedWidth = useWindowWidth();

    const [formError, onChangeFormError] = useState({});
    const [trunkId, onChangeTrunkId] = useState(null);

    let {formatMessage} = useIntl();

    const MIN_NUMBER = 1;
    const MAX_NUMBER = allocationLimit && allocationLimit.price_range 
        ? allocationLimit.price_range.once 
        : allocationLimit && allocationLimit.other && allocationLimit.other.once || 1000;

    const formSchemaInner = Schema.Model({
        trunk_id: StringType()
            .isRequired(formatMessage(m.thisFieldIsRequired)),
        numbers: NumberType( formatMessage(m.pleaseEnterValidNumber) )
            .min(MIN_NUMBER, formatMessage(m.minNumber, {number: MIN_NUMBER}))
            .max(MAX_NUMBER, formatMessage(m.maxNumber, {number: MAX_NUMBER}))
            .isRequired(formatMessage(m.thisFieldIsRequired)),
    });

    const onSubmit = async () => {
        const data = form.getFormValue();
        const response = await onSend({...data, numbers: Number(data.numbers), random_order: randomNumber});

        if (!response)
            return;

        if (response.status === 0) {
            clearForm();
        } else {
            const trunk = trunks.filter( trunk => trunk.id === trunkId );
            props.setReasonModalProps({
                reasonCode: response.reason_code,
                isRandom: randomNumber,
                method: ALLOCATION_PRICE_RANGE_DIALER_API,
                trunk: trunk[0],
                range: priceData.range_name,
                subdest: priceData.subdestination_name,
                numbersAmount: formValue.numbers,
                reasonHash: response.reason_hash
            });
        }
    };

    const clearForm = () => {
        onChangeFormValue({trunk_id: null, numbers: 1});
        onChangeRandomNumber(false);
    };

    const checkForm = (form, formSchema, formData, formError) => {
        if (!form.check()) {
            return true;
        } else {
            const checker = Object.keys(formSchema.check(formData)).map(i => formSchema.check(formData)[i].hasError).every(value => value === false) &&
                !(formData.hasOwnProperty('name') && formData.hasOwnProperty('numbers'));
            if (checker) {
                onSubmit();
                return false;
            }
        }
        return true;
    };

    const priceTrunks = trunks 
        ? trunks.filter(value => {
            return value.sp_key === priceData.sp_key && !value.closed && !value.option_google_otp
        })
        : {};

    const maxNumbersCalculated = priceData.available_numbers > MAX_NUMBER ? MAX_NUMBER : priceData.available_numbers;

    return (
        <ModalForm
            show={show}
            confirmButtonText={formatMessage(m.getNumbers)}
            onConfirm={() => {
                checkForm(form, formSchemaInner, formValue, formError);
                //    onChangeFormValue({trunk_id: '', numbers: 1});
            }}
            onClose={() => {
                onClose(false);
                clearForm();
            }}
            title={!spAuth 
                ? `${formatMessage(m.getNumbersForRange)} ${priceData.range_name}`
                : `${formatMessage(m.getNumbersForDestination, {name: priceData.subdestination_name})}`
            }
            width={resizedWidth > maxWidth ? maxWidth : resizedWidth}
            showFooter={true}
            whisper={priceData.range_name}
            disabled={trunkId === null}
            loading={loading}
        >
            <Form layout='horizontal'
                  ref={ref => (form = ref)}
                  model={formSchemaInner}
                  onChange={formValue => {
                      onChangeFormValue(formValue);
                  }}
                  onCheck={formError => {
                      onChangeFormError(formError);
                  }}
                  formValue={formValue}
            >
                <FormParts>
                    <FormPart>

                        <FormGroup>
                            <TrunkField>
                                <CustomField
                                    accepter={TrunksList}
                                    name='trunk_id'
                                    data={priceTrunks}
                                    onChange={(value) => onChangeTrunkId(value)}
                                />
                            </TrunkField>
                        </FormGroup>

                    </FormPart>
                    <FormPart>

                        <FormGroup>
                            <FlexBox>
                                <FormLabel>{formatMessage(m.getNumbersModal, {count: MAX_NUMBER})}</FormLabel>
                                <FormFieldWrapper width="80px">
                                    <FormControl
                                        accepter={BaseInputNumberField}
                                        max={maxNumbersCalculated}
                                        min={1}
                                        name='numbers'
                                    />
                                </FormFieldWrapper>
                            </FlexBox>
                        </FormGroup>

                    </FormPart>
                    <FormPart>
                        <BaseCheckboxField
                            //     ,       
                            onChange={(_, checked) => {
                                onChangeRandomNumber(checked)
                            }}
                            defaultChecked={randomNumber}
                            name="random_order"
                            value="random_order"
                        >
                            {formatMessage(m.randomOrder)}
                        </BaseCheckboxField>

                    </FormPart>
                </FormParts>
            </Form>
        </ModalForm>
    );
};

const FlexBox = styled.div`
    align-items: center;
    display: flex;
    flex-wrap: wrap;
`;

const FormParts = styled.div`
    display: block;
    margin-bottom: -20px;
    @media (min-width: ${md.min}px) {
        align-items: center;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
`;

const FormPart = styled.div`
    padding-bottom: 20px;

    @media (min-width: ${md.min}px) {
        padding-right: 10px;

        &:last-child {
            padding-right: 0;
        }
    }
`;

const FormLabel = styled(ControlLabel)`
    &&&& {
        float: none;
        margin: 0 0 10px 0;
        padding: 0;
        text-align: left;
        word-wrap: none;
        TrunkField
            width: auto;
        }
    }
`;

const FormFieldWrapper = styled.div`
    width: 100%;

    {${props => props.width && css`
        @media (min-width: ${md.min}px) {
            width: ${props.width}
        }
    `}
`;

const TrunkField = styled.div`
    width: 100%;

    @media (min-width: ${md.min}px) {
        width: 200px;
    }
`;

export const ModalForm = styled(CustomModalClient)`
    .rs-modal-content {
        display: flex;
        flex-direction: column;
    }

    .rs-modal-footer {
        margin-top: auto;
        padding-bottom: 20px;
        padding-top: 20px;
    }
`;